.Home {
    font-size: 1.2rem;
    margin-top: 100px;
    width: 54vw;
    text-align: center;
}

.quickStart {
    display: flex;
    justify-content: left;
    flex-direction: column;
    margin-top: 40px;
}

.quickStart .header {
    text-align: left;
    width: 650px;
    font-weight: bold;
    color: var(--color-accent);
    margin: 10px 0;
}

.quickStart .title {
    font-size: 2rem;
}

.quickStart .subtitle {
    font-size: 1.5rem;
}

.quickStart .content-text {
    margin: 10px 0;
    line-height: 1.4rem;
}