@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap);
:root {
  --color-primary: #000;
  --color-primary-light: rgba(0,0,0,0.3);
  --color-accent: #0496ff;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: 'PT Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.anim {
  transition: all .2s ease-in-out;
}

.vertical {
  height: 20px;
}

.anim {
  transition: all .2s ease-in-out;
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
.Body {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10vw;
}
.Footer {
    font-size: 0.9rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px 0;
    background-color: var(--color-accent);
    color: white;
    text-align: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.Footer .org:hover {
    cursor: pointer;
}

.NavigationBar {
    background-color: var(--color-accent);
    margin-bottom: 40px;
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    border-radius: 4px;
}

.NavigationBar .link {
    border-radius: 2px;
    padding: 4px 12px;
    background-color: var(--color-accent);
    color: white;
    font-weight: bold;
    font-size: 1.2rem;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin: 0 64px;
}

.NavigationBar .link:hover {
    color: var(--color-accent);
    background-color: white;
    cursor: pointer;
}

.NpmBar {
    position: absolute;
    top: 40px;
    right: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.NpmBar .iconWrapper {
    padding: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    border: 2px solid var(--color-accent);
}

.NpmBar .iconWrapper:hover {
    cursor: pointer;
}

.Headline {
    font-weight: bold;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: center;
    margin: 0 0 80px 0;
}

.Headline .title {
    color: var(--color-accent);
    font-size: 4rem;
    margin-bottom: 4px;
}

.Headline .subtitle {
    font-size: 1rem;
    color: rgba(0,0,0,0.4);
}


.Home {
    font-size: 1.2rem;
    margin-top: 100px;
    width: 54vw;
    text-align: center;
}

.quickStart {
    display: flex;
    justify-content: left;
    flex-direction: column;
    margin-top: 40px;
}

.quickStart .header {
    text-align: left;
    width: 650px;
    font-weight: bold;
    color: var(--color-accent);
    margin: 10px 0;
}

.quickStart .title {
    font-size: 2rem;
}

.quickStart .subtitle {
    font-size: 1.5rem;
}

.quickStart .content-text {
    margin: 10px 0;
    line-height: 1.4rem;
}
.TextArea {
    display: inline-block;
    background-color: rgba(4, 150, 255, 0.05);
    color: var(--color-accent);
    border-radius: 4px;
    align-self: center;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.TextArea:hover {
    background-color: rgba(4, 150, 255, 0.1);
}
